import React from 'react';
import styles from './Form.module.scss';

class Form extends React.Component {
  state = {
    nameMedicine: '',
    amountMedicine: '',
    dateMedicine: new Date().toISOString().slice(0, 10),
    remindMedicine: false,
  }

  handleInputChange = e => {
    if (e.target.type === 'checkbox') {
      this.setState({
        [e.target.id]: e.target.checked,
      })
    } else {
      this.setState(({
        [e.target.id]: e.target.value,
      }))
    }
  }

  handleClickSubmit = () => {
    const { nameMedicine, amountMedicine, dateMedicine, remindMedicine } = this.state;
    const newMedicine = this.props.addMedicine(nameMedicine.trim(), dateMedicine, amountMedicine, remindMedicine);
    if (newMedicine) {
      this.setState({
        nameMedicine: '',
        amountMedicine: '',
        dateMedicine: new Date().toISOString().slice(0, 10),
        remindMedicine: false,
      })
    }
  }


  render() {
    return (
      <div className={styles.wrapper}>
        <h1 className={styles.title}>Dodaj Lek</h1>
        <form className={styles.form}>
          <div className={styles.medicineWrapper}>

            <label htmlFor="nameMedicine" className={styles.medicineLabel}>
              <h2 className={styles.nameTitle}>Nazwa Leku:</h2>
              <input
                type="text"
                id="nameMedicine"
                className={styles.input}
                placeholder="Nazwa"
                autoComplete="off"
                value={this.state.nameMedicine}
                onChange={this.handleInputChange}

              />
            </label>
          </div>

          <div className={styles.medicineWrapper}>

            <label htmlFor="amountMedicine" className={styles.medicineLabel}>
              <h2 className={styles.amountTitle}>Ilość Tabletek:</h2>
              <input
                type="number"
                id="amountMedicine"
                className={styles.inputAmount}
                placeholder="0"
                max="999"
                value={this.state.amountMedicine}
                onChange={this.handleInputChange}

              />
            </label>
          </div>

          <div className={styles.medicineWrapper}>

            <label htmlFor="dateMedicine" className={styles.medicineLabel}>
              <h2 className={styles.dateTitle}>Data ważności:</h2>
              <input
                type="date"
                id="dateMedicine"
                className={styles.input}
                value={this.state.dateMedicine}
                onChange={this.handleInputChange}

              />
            </label>
          </div>

          <div className={styles.medicineWrapper}>

            <label htmlFor="remindMedicine" className={styles.medicineLabel}>
              <h2 className={styles.remindTitle}>Przypomnienia:</h2>
              <input
                type="checkbox"
                id="remindMedicine"
                className={styles.inputRemind}
                checked={this.state.remindMedicine}
                onChange={this.handleInputChange}

              />
            </label>
          </div>

        </form>

        <button
          onClick={this.handleClickSubmit}
          className={styles.btnAdd}
        >
          DODAJ
        </button>
      </div >
    )
  }
}

export default Form;